import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class SourceforgeService {
  

  constructor(private http: HttpClient, private parser: DOMParser) { }
  public getBuilds(url) {
    return this.http.get(url, { headers: {
      'Access-Control-Allow-Origin': '*',
      "Access-Control-Allow-Methods": "DELETE, POST, GET, OPTIONS",
      "Access-Control-Allow-Headers": "Content-Type, Access-Control-Allow-Headers, Authorization, X-Requested-With"
    } }).pipe(map(data => {
      //assuming only sf.net/p/PROJ/files/FOLNAME is provided only
      let builds = [];
      let parsedHtml = this.parser.parseFromString(data.toString(), 'text/html');

      let beeld = parsedHtml.querySelectorAll("#files_list > tbody > tr");
      for(let i=0; i<beeld.length; i++) {
        builds.push({
          title: (<HTMLElement>beeld[i]).title,
          date: (<HTMLElement>beeld[i].children[1]).innerText,
          size: (<HTMLElement>beeld[i].children[2]).innerText,
          count: (<HTMLElement>beeld[i].children[3]).innerText
        });
      }
      console.log(builds);
      return builds;
    }));
  }
}
